@tailwind base;
@tailwind components;
@tailwind utilities;

/* 1. Force the top banner iframe to be hidden */
/* This is the most important rule for the banner */
iframe.goog-te-banner-frame {
    display: none !important;
}

/* 2. Prevent the body from being pushed down by the (now hidden) banner */
body {
    top: 0px !important;
    position: relative !important; /* Ensure body positioning context is stable */
}

/* This is a wrapper for the widget provided in TranslationBar.js */
#translation-bar-container .goog-te-gadget-simple {
    background-color: transparent !important;
    border: none !important;
}

/* 3. Hide the "Powered by" text and Google logo */
/* The text is inside a span that we can hide */
#translation-bar-container .goog-te-gadget-simple > span {
    display: none;
}
/* The logo is an image */
#translation-bar-container .goog-te-gadget-simple img {
    display: none !important;
}

/* 4. Style the dropdown <select> element to match your UI */
#translation-bar-container select.goog-te-combo {
    cursor: pointer;
    background-color: #4B5563; /* Tailwind gray-600 */
    color: white;
    border: none;
    border-radius: 4px;
    padding: 4px 8px; /* A bit more padding for easier clicking */
    font-size: 0.75rem; /* text-xs */
    height: 28px; /* Set a fixed height */
}

/* 5. Style the options within the dropdown for readability */
#translation-bar-container select.goog-te-combo option {
    background-color: #374151; /* gray-700 */
    color: white;
}
